body {
  background: #1f2833;
}
.display-time {
    font-size: 1.5rem;
    width: 4rem;
}
.display-distance {
    font-size: 1.5rem;
    width: 7rem;
}
.board {
    max-width: 25rem;
    margin-right: auto;
    margin-left: auto;
    font-size: 1.5rem;
    color: #c5c6c7;

}
.row {
    display: flex;
    align-items: center;
}
.header {
    background-color: #0b0c10;
    /* color: #66fcf1; */
    color: #45a29e;
    height: 20vh;
    
}

.title {
    align-items: center;
    position: relative;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    font-size: 3rem;
}
